.design-hottub__media {
    margin-top: -200px !important;
}
.design-hottub__media img {
    width: 100%;
}
    .design-hottub__setting {
    display: flex;
    justify-content: center;
}
.design-hottub__setting .setting-type {
    margin: 0 20px;
}
.design-hottub__setting .setting-type .setting-type__subject {
    display: flex;
    align-items: center;
}
.setting-type__subject .setting-type__title {
    margin: 10px 10px !important;
    color: rgb(80, 80, 80) !important;
    font-weight: bold;
    font-size: 19px;
    white-space: nowrap;
}
.setting-type__subject .setting-type__name {
    margin: 10px 0 !important;
    color: rgb(80, 80, 80) !important;
    font-size: 19px;
    white-space: nowrap;
}
.design-hottub__setting .setting-type__images {
    display: flex;
}
.design-hottub__setting .setting-type__image {
    position: relative;
    margin: 6px;
    width: 45px;
    height: 45px;
    cursor: pointer;
}
.design-hottub__setting .setting-type__image::before {
    position: absolute;
    content: '';
    width: calc(100% + 8px);
    height: calc(100% + 8px);
    top: 50%;
    left: 50%;
    border-radius: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid transparent;
    transition: 0.3s;
}
.design-hottub__setting .setting-type__image.curSel::before {
    border-color: rgb(50, 50, 50);
}
.design-hottub__setting .setting-type__image:hover::before {
    border-color: rgb(150, 150, 150);
}
.design-hottub__setting .setting-type__image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
}